*,
*::before,
*::after {
  margin: 0;
}

.projects {
    /* font-family: "Quicksand", sans-serif; */
    display: grid;
    grid-template-rows: 3em auto 3em;
    /* place-items: center; */
    height: 100vh;
    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
    overflow-y: auto;
}

a {
    color: white;
    margin-top: 1.5em;
    margin-left: 1.3em;
    width: min-content;
    text-decoration: none;
    display: inline-block;
    width: fit-content;
}

a:hover { COLOR: #a8bbbf; TEXT-DECORATION: none; font-weight: none }

.header_icon{
    position: fixed;
}

.reg_link, .router_link {
    margin: 0;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-self: center;
    justify-content: center;
    max-width: 1200px;
    margin-block: 2rem;
    gap: 2rem;
}

.card {
    display: flex;
    flex-direction: column;
    width: clamp(20rem, calc(20rem + 2vw), 22rem);
    height: min-content;
    overflow: hidden;
    border-radius: 1em;
    background: #413F42;
    color: white;
}

.in-card {
    /* width: 80%; */
    color: white;
}

img, video {
    max-width: 100%;
    border-radius: 1em;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.card__body {
    padding: 1rem;
    /* display: flex; */
    flex-direction: column;
    gap: .5rem;
}

h2 {
    margin-top: 0.5em;
}


p, ul {
    font-size: 1.1em;
}

ul {
    list-style-type: circle;
}

li {
    margin: 10px 0;
}

.tag {
    align-self: flex-start;
    padding: .25em .75em;
    border-radius: 1em;
    font-size: .75rem;
}

.card__footer {
    padding-left: 1em;
    padding-right: 1em;
    padding-bottom: 1em;
    margin-top: auto;
    color: rgb(255, 255, 255);
    font-size: 1.2em;
}

.project_footer{
    color: white;
}