@import url(https://fonts.googleapis.com/css2?family=Revalia&display=swap);
body {
  margin: 0;
  padding:0;
  background-color: rgb(23, 23, 23);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.footer-container {
    /* position: absolute;
    bottom: 0;
    margin: 1.5em; */

    position: fixed;
    display: flex;
    left: 50%;
    bottom: 0px;
    transform: translate(-50%, -25%);
}

.icon {
    color: white;
    padding: 0.3em;
}
.slideContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;

    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
}
.slideGrid {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    justify-items: center;
}
.slide {
    grid-row: 2/3;
}
.slideNav {
    position: relative;
    text-align: center;
}

.text {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    font-size: 250%;
    color: white;
}

.letter, .letterContainer {
    display: inline-block;
    padding: 0.1em;
}

.space {
    padding-right: 0.5em;
}

::-webkit-scrollbar {
  width: 20px;
}

/* ::-webkit-scrollbar-track {
  background-color: transparent;
} */

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
}

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
  }
  
::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}
  
@media all and (max-width: 768px){
  ::-webkit-scrollbar {
      width: 0;
  }
}
*,
*::before,
*::after {
  margin: 0;
}

.projects {
    /* font-family: "Quicksand", sans-serif; */
    display: grid;
    grid-template-rows: 3em auto 3em;
    /* place-items: center; */
    height: 100vh;
    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(63, 6, 50) 75%);
    overflow-y: auto;
}

a {
    color: white;
    margin-top: 1.5em;
    margin-left: 1.3em;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    text-decoration: none;
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

a:hover { COLOR: #a8bbbf; TEXT-DECORATION: none; font-weight: none }

.header_icon{
    position: fixed;
}

.reg_link, .router_link {
    margin: 0;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-self: center;
    justify-content: center;
    max-width: 1200px;
    margin-block: 2rem;
    grid-gap: 2rem;
    gap: 2rem;
}

.card {
    display: flex;
    flex-direction: column;
    width: clamp(20rem, calc(20rem + 2vw), 22rem);
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    overflow: hidden;
    border-radius: 1em;
    background: #413F42;
    color: white;
}

.in-card {
    /* width: 80%; */
    color: white;
}

img, video {
    max-width: 100%;
    border-radius: 1em;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.card__body {
    padding: 1rem;
    /* display: flex; */
    flex-direction: column;
    grid-gap: .5rem;
    gap: .5rem;
}

h2 {
    margin-top: 0.5em;
}


p, ul {
    font-size: 1.1em;
}

ul {
    list-style-type: circle;
}

li {
    margin: 10px 0;
}

.tag {
    align-self: flex-start;
    padding: .25em .75em;
    border-radius: 1em;
    font-size: .75rem;
}

.card__footer {
    padding-left: 1em;
    padding-right: 1em;
    padding-bottom: 1em;
    margin-top: auto;
    color: rgb(255, 255, 255);
    font-size: 1.2em;
}

.project_footer{
    color: white;
}
